import { Component } from "react";
import * as React from "react";
import { connect } from "react-redux";
import { updateTaskItemAction } from "actions/task";
import SubTaskItem from "./components/SubTaskItem";
import "./scroll.css";

class SubTaskList extends Component {
  renderList() {
    const unChecked = (
      <img
        className=" rounded float-left"
        width="30px"
        height="30px"
        src="/untick.svg"
        alt="tick sign"
      />
    );
    const Checked = (
      <img
        className=" rounded float-left"
        width="30px"
        height="30px"
        src="/tick.svg"
        alt="tick sign"
      />
    );

    const { tasks, task, subTasks } = this.props;
    let index = -1;
    if (subTasks) {
      return subTasks.map((subTask) => {
        const indexCurr = index + 1;
        index += 1;
        return (
          <div key={subTask._id}>
            <SubTaskItem
              task={task}
              tasks={tasks}
              subTasks={subTasks}
              subTask={subTask}
              index={indexCurr}
              Checked={Checked}
              unChecked={unChecked}
            />
          </div>
        );
      });
    }
    return <div />;
  }

  render() {
    const scrollContainerStyle = { maxHeight: "250px" };
    return (
      <div
        className="container scrollbar scrollbar-warning mx-auto"
        style={scrollContainerStyle}
      >
        {this.renderList()}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { tasks: state.tasks };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateTask: (task) => dispatch(updateTaskItemAction(task)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubTaskList);
